import fetch from "isomorphic-unfetch"

const Page = ({ stars }) =>
  <div>
    Next stars: {stars}
  </div>

Page.getInitialProps = async ({ req }) => {
  const res = await fetch('https://localhost:8080/brags')
  const json = await res.json()
  console.log(json)
  return { stars: json.brags }
}

export default Page
